@use 'base/variables' as v;
@use 'base/mixins' as m;

footer {
    color: v.$blanco;
    // background-color: rgb(0 0 0 / 60%);
    background-color: v.$negro;
    height: 30rem;

    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    p {
        text-align: center;
        font-size: 1.6rem;
        margin-bottom: 2rem;
    }

    .iconosFlex {
        display: flex;
        gap: 3rem;

        .iconosFooter {
            height: 6rem;
            width: 6rem;
            background-color: v.$blanco;
            border-radius: 50%;
            display: flex;
            justify-content: center;
            align-items: center;

            img {
                width: 3rem;
            }
        }
    }

}